import { request } from './index';

// 
export const getSearchAssociation = function (params) {
  return request({
    url: '/app/v1_0/suggestion',
    params,
  });
};

// 
export const getSearchInfo = function (params) {
  return request({
    url: '/app/v1_0/search',
    params,
  });
};

// 
export const getSearchHistory = function () {
  return request({
    url: '/app/v1_0/search/histories',
  });
};
